import {profileAPI} from "../api/api";
import {stopSubmit} from "redux-form";
import {PhotosType, PostType, ProfileType} from "../Types/Types";

const Add_Post = 'ADD-POST';
const set_User_Profile = 'SET_USER_PROFILE';
const set_Status = 'SET_STATUS';
const save_Photos_Success = 'SAVE_PHOTOS_SUCCESS';



let initialState = {
    posts: [
        {id: 1, message: 'Hello i have registered in this social network', likesCount: 20},
        {id: 2, message: 'It`s fantastic!', likesCount: 15}
    ] as Array<PostType>,
    profile: null as ProfileType | null,
    status: ""
}

export type initialStateType = typeof initialState;

const profileReducer = (state = initialState, action: any): initialStateType => {

    switch (action.type) {
        case Add_Post:
            let newPost = action.newPostText;
            return  {
                ...state,
                posts: [...state.posts, {id: 3, message: newPost, likesCount: 0}]
            }
            case set_User_Profile:
            return  {
                ...state,
                profile: action.profile
            }
            case set_Status:
            return  {
                ...state,
                status: action.status
            }
            case save_Photos_Success:
            return  {
                ...state,
                profile: {...state.profile, photos:action.photos} as ProfileType
            }
        default:
            return state;
    }

    return state;
}

type addPostActionCreatorType = {
    type: typeof Add_Post
    newPostText: string
}
type setUserProfileActionType = {
    type: typeof set_User_Profile
    profile: ProfileType
}
type setStatusActionType = {
    type: typeof set_Status
    status: string
}
type savePhotoSuccessActionType = {
    type: typeof save_Photos_Success
    photos: PhotosType
}

export const addPostActionCreator = (newPostText: string): addPostActionCreatorType =>
    ({type: Add_Post, newPostText});
export const setUserProfile = (profile: ProfileType): setUserProfileActionType => ({type: set_User_Profile, profile});
export const setStatus = (status: string): setStatusActionType => ({type: set_Status, status});
export const savePhotoSuccess = (photos: PhotosType): savePhotoSuccessActionType => ({type: save_Photos_Success, photos});

export const getUserProfile = (userId: number) => async (dispatch: any) => {
   let response = await profileAPI.getProfile(userId);
        dispatch(setUserProfile(response.data));
};
export const getStatus = (userId: number) => async (dispatch: any) => {
   let response = await profileAPI.getStatus(userId);
        dispatch(setStatus(response.data));
};
export const updateStatus = (status: string) => async (dispatch: any) => {
   let response = await profileAPI.updateStatus(status)
        if (response.data.resultCode === 0) {
        dispatch(setStatus(status));
        }
};
export const savePhoto = (file: any) => async (dispatch: any) => {
   let response = await profileAPI.savePhoto(file)
        if (response.data.resultCode === 0) {
        dispatch(savePhotoSuccess(response.data.data.photos));
        }
};
export const saveProfile = (profile: ProfileType) => async (dispatch: any, getState: any) => {
    const userId = getState().auth.userId;
   let response = await profileAPI.saveProfile(profile);
        if (response.data.resultCode === 0) {
        dispatch(getUserProfile(userId));
        } else {
            dispatch(stopSubmit("edit-profile", {_error: response.data.messages[0]}));
            return Promise.reject(response.data.messages[0]);
        }
}


export default profileReducer;